.animatedbg {
    width: 100vw;
    height: 100vh;
    color: #fff;
    background: -webkit-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB, #F7EA00);
    background: -o-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB, #F7EA00);
    background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB, #F7EA00);
    background-size: 400% 400%;
    -webkit-animation: Gradient 15s ease infinite;
    animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient {
    0% {
        background-position: 0 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0 50%
    }
}

@keyframes Gradient {
    0% {
        background-position: 0 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0 50%
    }
}




